import san from "@/assets/san.png";
import chainIcon from "@/assets/chainIcon.png";
export default {
  data() {
    return {
      showPositionValue: false, //提示信息
      toastContent: "",
      topTitle: "修改提币地址",
      san: san,
      chainIcon:chainIcon,
      form: {
        coin_type: "",
        chain: "",
        setting: false,
        address: "",
        remark: "",
        memo:""
      }
    };
  },
  filters: {
    uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }
  },
  mounted(){
    this.getEdit();
  },
  methods: {
    getEdit(){
       var that=this,
      formData={
         sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          address_id:that.$route.params.id
      }
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/personal/edit-address",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.form=response.data;
          if(response.data.setting==1){
            that.form.setting=true;
          }else{
            that.form.setting=false;
          }
        })
        .catch(function(error) {
        console.log(error)
        });
    },
     // 保存
    getSubmt() {
      var that = this,
        formData,
        setVal;
      if (that.form.setting == false) {
        setVal = 2;
      } else {
        setVal = 1;
      }
      if (that.form.address == "") {
        that.showPositionValue = true;
        that.toastContent = "请您输入地址";
      } else if (that.form.remark == "") {
        that.showPositionValue = true;
        that.toastContent = "请您输入备注";
      } else {
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          address_id:that.$route.params.id,
          coin_type: that.form.coin_type,
          chain: that.form.chain,
          address: that.form.address,
          setting: setVal,
          remark: that.form.remark
        };
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/personal/update-address",
            data: that.$qs.stringify(formData)
          })
          .then(function(response) {
            if (response.code == 204) {
              that.showPositionValue = true;
             that.toastContent = response.errmsg;
            }else{
               that.$router.push("/wallet/address");
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
     // 币链接信息介绍
    getInfo() {
      this.$router.push("/chainCoinInfo");
    }
  }
};



